body {
    margin: 0;
    padding: 0;
    font-family: Arial;
    display: flex;
}

.menu {
    position: fixed;
    width: 250px;
    background-color: #333;
    color: #FFF;
    padding: 20px;
    top: 0;
    bottom: 0;
    left: 0;
    right: auto;
}

.menu ul {
    list-style: none;
    padding: 0;
}

.menu button {
    background: none;
    transition: background 0.3s ease;
    border: none;
    color: #FFF;
    display: block;
    padding : 15px 0;
    width : 250px;
}

.menu button:hover {
    background: #444;
}

.main-contents {
    flex: 1;
    padding: 0 0;
    background-color: #FFF;
    margin-left: 300px;
    margin-top: 0;
}